import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import {logout} from '../action/userAction'
import { Avatar } from "@material-ui/core";
import { Link } from "react-router-dom";

export default function UserNav() {
    const userInfo = useSelector((state) => state.userStatus.userInfo);
    const dispatch = useDispatch()
    const logoutHandler = () => {
        dispatch(logout())
    }
    useEffect(() => {
        const userNameContainer = document.getElementById("userName");
        const userNameText = document.querySelector("#userName span");

        if (userNameContainer && userNameText) {
            while (userNameText.style.width > userNameContainer.style.width) {
                userNameText.style.width = parseInt(userNameText.style.width) - 1 + "px";
            }
        }

        return () => {
            //
        };
    }, []);
    return (
        <nav className="user-nav position-fixed h-100">
            <div className="bg-gradient">
                <div className="navbar d-flex flex-column p-0">
                    <div>
                        <div className="userbrief">
                            <Avatar style={{width:70,height:70}} src ={userInfo.userPic}/>
                            <div className="user-name" id="userName">
                                <span>{userInfo.name}</span>
                            </div>
                        </div>
                    </div>
                    <div className="nav-n-form">
                        <div className="">
                            <ul className="navbar-nav d-flex flex-column navbar-expand">
                                <li className="nav-item">
                                    <Link to='/' className="nav-link">
                                        <i className="fas fa-home"></i>
                                        <span className="link-text">Home</span>
                                    </Link>
                                </li>
                                <li className="nav-item">
                                    <Link to='/inprogress' className="nav-link">
                                        <i className="fas fa-book"></i>
                                        <span className="link-text">My course</span>
                                    </Link>
                                </li>
                                <li className="nav-item">
                                    <Link to='/completed' className="nav-link">
                                        <i className="fas fa-history"></i>
                                        <span className="link-text">Completed</span>
                                    </Link>
                                </li>
                                <li className="nav-item">
                                    <a onClick={logoutHandler} className="nav-link">
                                        <i className="fas fa-sign-out-alt"></i>
                                        <span className="link-text">Log out</span>
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            <div className="myshadow"></div>
        </nav>
    );
}
