import React, { useEffect, lazy, Suspense } from "react";
import { useSelector, useDispatch } from "react-redux";
import { ScrollToTop } from "./Component/commonComponent/ScrollToTop";

import { Route } from "react-router-dom";
import { Header } from "./Component/commonComponent/Header";

import Axios from "axios";
import UserNav from "./Component/UserNav";
import GuestNav from "./Component/GuestNav";
import { fetchUserData } from "./action/userAction";
import { ScrollTopButton } from "./Component/ScrollTopButton";
import { Fab, Toolbar } from "@material-ui/core";
import KeyboardArrowUpIcon from "@material-ui/icons/KeyboardArrowUp";
const UserHome = lazy(() => import("./pageConponent/UserHome"));
const Detail = lazy(() => import("./pageConponent/Detail"));
const Partner = lazy(() => import("./pageConponent/Partner"));
const LandingPage = lazy(() => import("./pageConponent/LandingPage"));
const Linhvuc = lazy(() => import("./pageConponent/Linhvuc"));
const Completed = lazy(() => import("./pageConponent/Completed"));
const Inprogress = lazy(() => import("./pageConponent/Inprogress"));
const Cart = lazy(() => import("./pageConponent/Cart"));
const Footer = lazy(()=> import("./Component/commonComponent/Footer")) ;

Axios.defaults.withCredentials = true;
function App(props) {
    const userStatus = useSelector((state) => state.userStatus);
    const { auth } = userStatus;
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(fetchUserData());
        return () => {};
    }, []);
    return (
        <div>
            <Toolbar style={{ position: "absolute" }} id="back-to-top-anchor" />
            {auth ? <Header addStyle="position-sticky sticky-top" /> : <Header addStyle="" />}
            {auth ? <UserNav /> : <GuestNav />}
            <Suspense fallback={<div>loading...</div>}>
                <Route exact path="/" component={LandingPage} />
                <Route path="/home" component={UserHome} />
                <Route path="/detail/:id" component={Detail} />
                <Route path="/partner" component={Partner} />
                <Route path="/linhvuc" component={Linhvuc} />
                <Route path="/cart" component={Cart} />
                <Route path="/completed" component={Completed} />
                <Route path="/inprogress" component={Inprogress} />
                <Footer />
            </Suspense>
            <ScrollToTop />
            <ScrollTopButton {...props}>
                <Fab color="secondary" size="medium" aria-label="scroll back to top">
                    <KeyboardArrowUpIcon />
                </Fab>
            </ScrollTopButton>
            
        </div>
    );
}
export default App;
