import React from "react";
import { IconButton, Badge } from "@material-ui/core";
import {  ShoppingCartOutlined } from "@material-ui/icons";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import { NoticficationButton } from "../NoticficationButton";

export const Header = (props) => {
    const userStatus = useSelector((state) => state.userStatus);

    return (
        <header  className={`${props.addStyle} w-100`}>
            <div className=" d-flex justify-content-be align-content-center align-items-center header mx-auto p-2">
                <Link to="/" className="logo">
                    VietCourses
                </Link>
                <div className="dropdown subject-dropdown">
                    <button type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown">
                        Danh mục
                    </button>
                    <div className="dropdown-menu">
                        <a className="dropdown-item" href="#">
                            Nghiên cứu khoa học
                        </a>
                        <a className="dropdown-item" href="#">
                            Công nghệ thông tin
                        </a>
                        <a className="dropdown-item" href="#">
                            Thiết kế đồ họa
                        </a>
                        <a className="dropdown-item" href="#">
                            Marketing-Truyền thông
                        </a>
                        <a className="dropdown-item" href="#">
                            Kinh doanh
                        </a>
                        <a className="dropdown-item" href="#">
                            Ngoại ngữ
                        </a>
                        <a className="dropdown-item" href="#">
                            Âm nhạc
                        </a>
                        <a className="dropdown-item" href="#">
                            Khoa học dữ liệu
                        </a>
                        <a className="dropdown-item" href="#">
                            Khoa học máy tính
                        </a>
                        <a className="dropdown-item" href="#">
                            Sức khỏe
                        </a>
                    </div>
                </div>
                <form className="seach-form d-inline-flex align-content-center">
                    <input type="text" name="" placeholder="Tìm kiếm..." />
                    <button type="submit">
                        <i className="fas fa-search"></i>
                    </button>
                </form>
                {userStatus.userInfo ? (
                    <>
                        <NoticficationButton/>
                        <Link to="/cart">
                            <IconButton aria-label="cart">
                                <Badge
                                    badgeContent={userStatus.userInfo.cart.length}
                                    color="secondary"
                                    anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
                                >
                                    <ShoppingCartOutlined />
                                </Badge>
                            </IconButton>
                        </Link>
                    </>
                ) : (
                    ""
                )}
            </div>
        </header>
    );
};
