import React, { useEffect } from "react";
import $ from "jquery";
import { Link } from "react-router-dom";
export default function GuestNav() {
    useEffect(() => {
        $(document).scroll(function () {
            var y = $(this).scrollTop();
            let secondform = $("#secondary-seach");
            if (y > 72) {
                secondform.css("opacity", 1);
            } else {
                secondform.css("opacity", 0);
            }
        });
        return () => {
            //
        };
    }, []);
    return (
        <nav className="guest-nav">
            <div className="bg-gradient ">
                <div className="navbar d-flex justify-content-between mx-auto p-0 ">
                    <div className="nav-n-form">
                        <div className="d-flex flex-row justify-content-between">
                            <ul className="navbar-nav navbar-expand">
                                <li className="nav-item ">
                                    <Link to="/" className="nav-link">
                                        Trang chủ
                                    </Link>
                                </li>
                                <li className="nav-item ">
                                    <Link to="/partner" className="nav-link">
                                        Các bên hợp tác
                                    </Link>
                                </li>
                            </ul>
                            <form className="seach-form align-content-center mx-3" id="secondary-seach">
                                <input type="text" name="" placeholder="Tìm kiếm..." />
                                <button type="submit">
                                    <i className="fas fa-search"></i>
                                </button>
                            </form>
                        </div>
                    </div>
                    <div className="d-flex justify-content-around btn-grp">
                        <button className="flex-fill button btn-blue" id="login">
                            Đăng nhập
                        </button>
                        <button className="flex-fill button btn-outline" id="signup">
                            Đăng ký
                        </button>
                    </div>
                </div>
            </div>
            <div className="myshadow"></div>
        </nav>
    );
}
