import React, { useState } from "react";
import { IconButton, Badge, Popover } from "@material-ui/core";
import { NotificationImportantOutlined } from "@material-ui/icons";
import { useFirestoreConnect, useFirestore } from "react-redux-firebase";
import { useSelector } from "react-redux";
import { Noticfication } from "./Noticfication";


export const NoticficationButton = () => {
    const userStatus = useSelector(state => state.userStatus)
    const id = userStatus.userInfo && userStatus.userInfo._id
    const firestore = useFirestore()
    useFirestoreConnect(()=>[
        {collection:'noticfications',where:[['userId','==',id]],queryParams: [ 'orderByChild=Date' ]}
    ])
    const noticfications = useSelector(
        ({ firestore: { ordered } }) => ordered.noticfications && Object.values(ordered.noticfications)
      )
    const activeNotic = noticfications && noticfications.reduce((sum,notic)=>{return notic.seen?sum:sum+1},0)
    const [anchorEl, setAnchorEl] = useState(null);
    const closeHandler = () =>{
        setAnchorEl(null)
        const colection = firestore.collection("noticfications")
        colection.where('seen','==',false).get().then((data)=>{
            let batch = firestore.batch()
            data.docs.forEach((notic)=>{
                batch.update(notic.ref,{seen:true})
            })
            batch.commit()
        })

    }
    const openHandler = (e) =>{
        setAnchorEl(e.currentTarget)
    }
    const open = Boolean(anchorEl)
    return (
        <div>
            <IconButton aria-label="noticfication" onClick={openHandler}>
                <Badge badgeContent={activeNotic} color="secondary" anchorOrigin={{ vertical: "bottom", horizontal: "right" }}>
                    <NotificationImportantOutlined />
                </Badge>
            </IconButton>
            <Popover
                open={open}
                anchorEl={anchorEl}
                onClose={closeHandler}
                anchorOrigin={{
                    vertical: "bottom",
                    horizontal: "center",
                }}
                transformOrigin={{
                    vertical: "top",
                    horizontal: "center",
                }}
            >
                {noticfications && noticfications.map((notic)=>{
                    return <Noticfication data={notic}/>
                })}
            </Popover>
        </div>
    );
};
